import React, { Component } from 'react';
import { Container, Flex } from 'theme/grid';
import { Title, VideoContainer, StyledPlayButton } from './AboutMe.style';
import ReactPlayer from 'react-player';
import { H2, P } from 'theme/types';
import ToggleState from 'react-toggle-state';

class AboutMe extends Component {
    render () {
        return (
            <Container>   
                <Title>About AJ</Title>
                <H2 align="right">Wha wha wha about my hAollydays at Ile de Beaut!? Awhooooo!</H2>
                <Flex>
                    <P align="right">This mourning I was
                    walking along the bay gettin' to meet the dogs in town, there were Wadadi wadada...</P>
                    <ToggleState>
                    {({ isTrue: playing, turnTrue: startPlay, turnFalse: stopPlay}) =>
                        <ToggleState>
                        {({ isTrue: hovering, turnTrue: onMouseOver, turnFalse: onMouseLeave }) => 
                            <VideoContainer
                                onMouseLeave={onMouseLeave}
                                onMouseOver={onMouseOver}
                                onClick={playing ? stopPlay : startPlay}

                            >
                                <StyledPlayButton playing={playing} hover={hovering}/>
                                <ReactPlayer
                                loop
                                playing={playing}
                                height="auto"
                                width="100%"
                                url={require("assets/divaPepeul.mov")}/>
                            </VideoContainer>
                        }
                        </ToggleState>
                    }
                    </ToggleState>
                </Flex>
            </Container>
        );   
    }
}

export default AboutMe;




