import React from 'react';
import { Motion, spring } from 'react-motion';

export default function PlayButton({hover, ...rest}) {
    return(
    <Motion style={{offset: spring(hover ? 0 : 306 ) }}>
        {({ offset }) =>
        <svg width='110' height='119' viewBox='0 0 110 119' xmlns='http://www.w3.org/2000/svg'  {...rest}>
            <g id='Page-1' fill='none' fillRule='evenodd'>
                <g strokeDasharray={306} strokeDashoffset={ offset } id='Group' transform='translate(0 3)'>
                    <path d='M38,30.5 C38,28.0147186 40.0147186,26 42.5,26 C44.9852814,26 47,28.0147186 47,30.5 C47,32.9852814 44.9852814,35 42.5,35'
                    id='Oval' stroke='#FFF' strokeWidth='2' fill='#FFF' />
                    <path d='M0,55 L110,55' id='Path-2' stroke='#FFF' strokeWidth='2' />
                    <polygon id='Path-3' stroke='#FFF' strokeWidth='2' points='19 56.9185434 19 1 96 56.1225197 19 111'
                    />
                    <polygon id='Path-4' stroke='#FFF' strokeWidth='3' fill='#8B572A' points='20 22 20 0 39 11'
                    />
                    <path d='M49,15 L34,31' id='Path-5' stroke='#FFF' strokeWidth='3' />
                    <polygon id='Path-6' stroke='#FFF' fill='#9B9B9B' points='66 55 71.985741 38 97 55'
                    />
                    <polygon id='Path-7' stroke='#8B572A' strokeWidth='2' points='38 98.2223961 38 72 61 91.5427955 38 111'
                    />
                    <path d='M38,30.5 C38,28.0147186 40.0147186,26 42.5,26 C44.9852814,26 47,28.0147186 47,30.5 C47,32.9852814 44.9852814,35 42.5,35'
                    id='OvalB' stroke='#000' strokeWidth='4' fill='#000' />
                    <path d='M0,55.5 L110,55.5' id='Path-2B' stroke='#FFF' strokeWidth='3'
                    />
                    <polygon id='Path-3B' stroke='#FFF' strokeWidth='2' points='19 56.9185434 19 1 96 56.1225197 19 111'
                    />
                    <polygon id='Path-4B' stroke='#8B572A' strokeWidth='5' fill='#8B572A'
                    points='20 22 20 0 39 11' />
                    <path d='M49,15 L34,31' id='Path-5B' stroke='#000' strokeWidth='4' />
                    <polygon id='Path-6B' stroke='#000' strokeWidth='4' fill='#000' points='66 55 71.985741 38 97 55'
                    />
                    <polygon id='Path-7B' stroke='#8B572A' strokeWidth='3' points='38 98.2223961 38 72 61 91.5427955 38 111'
                    />
                </g>
            </g>
        </svg>
        }
    </Motion>
    );
}


